<template>
  <section class="loginsection">
    <div class="sectionTop">
      <h3></h3>
    </div>
    <div class="sectionCon">
      <div class="firstcon">
        <input type="radio" name="reg">
        <h3></h3>
        <p>.Amazon</p>
      </div>
      <div class="firstcon firstspecial">
        <input type="radio" name="reg" checked>
        <h3></h3>
        <p>.</p>
      </div>
      <div class="secondcon">
        <input type="text" placeholder="/" class="amazonphone" v-model="username">
        <input type="password" placeholder="Amazon" v-model="userpass">
        <!--<input type="text" placeholder="Amazon" style="display: none;" class="amazonpass" v-if="show" v-show="show">-->
      </div>
      <div class="thirdcon">
        <input type="checkbox" class="checkshow">
        <p></p>
        <span></span>
      </div>
      <div class="thirdcon">
        <input type="checkbox">
        <p></p>
      </div>
      <div class="forthcon">
        <input type="button" value="" id="loginbtn" class="loginbtn" v-on:click="recheck">
      </div>
      <div class="fifthcon">
        <p></p>
        <p><a href="#"></a></p>
      </div>
    </div>
    <div class="sectionBot">
      <p>@1999-2018,Amazon.com,inc,or its affiliates</p>
    </div>
  </section>
</template>

<script>
    import login from "../../assets/js/login"
    export default {
      name: "AmazonLoginSection",
      components: {login},
      data() {
        return {
          username: "",
          userpass: ""
        }
      },
      methods: {
        recheck: function (username) {
          // console.log(this.username)
          if (this.username == "18893782758") {
            if (this.userpass = "666") {
              alert("")
              // this.$router.push({path:'/mypage'})
              // this.$router.push({
              //   path: '/mypage',
              //   name: 'mypage',
              //   params: {
              //     username:username
              //   }
                this.$router.push({name:'mypage',query:{username:'666'}});
              // })
            } else {
              alert("")
            }
          }
        }
      }
    }
</script>

<style scoped>
  @import "../../assets/css/login.css";
</style>



